<template>
    <div class="list-wrapper">
        <div class="wrapper-title">
            <div>文章列表</div>
            <a-button type="primary" id="writeBtn" shape="round" @click="writeArticle" style="align-self: flex-end;">
                写文章
            </a-button>
        </div>
        <div class="list-box" v-if="articleList.length">
            <div class="article-item" v-for="item in articleList" :key="item.id">
                <div class="article-author">来自 {{ item.author }}</div>
                <div class="article-title">{{ item.title }}</div>
                <div class="article-content">{{ item.content }}</div>
                <div class="article-time">
                    创建于&emsp;{{ item.createTime }}&emsp;&emsp;&emsp;&emsp;更新时间&emsp;{{ item.updateTime }}
                </div>
                <img class="article-pic" src="../assets/default.jpg" />
                <div class="article-operate">
                    <a-button @click="operate(item, 0)" type="primary" shape="round">
                        查看
                    </a-button>
                    <a-button @click="operate(item, 1)" type="primary" shape="round">
                        编辑
                    </a-button>
                    <a-button @click="operate(item, 2)" type="primary" shape="round">
                        删除
                    </a-button>
                </div>
            </div>
        </div>
        <div class="list-box nodata" v-if="!articleList.length">
            暂无文章
        </div>
    </div>
</template>
<script>
import { onMounted, getCurrentInstance, reactive, ref } from 'vue'
export default {
    setup() {
        const { ctx } = getCurrentInstance()
        let articleList = ref([])
        let totalRecords = ref(0)
        const params = reactive({
            pageSize: 10,
            pageNumber: 1,
        })
        // 获取文章列表
        const getArticleList = () => {
            articleList.value = []
            ctx.$api.article.getArticleList(params).then((res) => {
                res.data.datas.forEach((item) => {
                    articleList.value.push(item)
                })
                totalRecords = res.data.totalRecords
            })
        }
        // 文章操作
        const operate = (data, type) => {
            if (type == 0) {
                //查看
                ctx.$store.commit('setCurWritingArticleId', data.id)
                ctx.$router.push({ path: '/home/read_article' })
            } else if (type == 1) {
                //编辑
                ctx.$store.commit('setCurWritingArticleId', data.id)
                ctx.$router.push({ path: '/home/write_article' })
            } else if (type == 2) {
                //删除
                ctx.deleteArticle(data.id)
            }
        }

        // 根据id删除文章
        let deleteArticle = (id) => {
            ctx.$api.article.deleteArticle(id).then(() => {
                ctx.$message.success('删除成功')
                getArticleList()
            })
        }

        //写文章
        let writeArticle = () => {
            ctx.$router.push({ path: '/home/write_article' })
        }

        onMounted(() => {
            ctx.$store.commit('setCurWritingArticleId', null)
            getArticleList()
        })
        return {
            articleList,
            totalRecords,
            params,
            operate,
            deleteArticle,
            writeArticle,
        }
    },
}
</script>
<style lang="scss">
.list-wrapper {
    width: 100%;
    height: 100%;

    .wrapper-title {
        width: 100%;
        display: flex;
        justify-content: space-between;
        margin-bottom: 20px;
        align-items: center;
        font-size: 24px;
    }

    .list-box {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;

        &.nodata {
            justify-content: center;
            align-items: center;
        }

        .article-item {
            width: 100%;
            display: flex;
            flex-direction: column;
            // height: 150px;
            justify-content: space-between;
            margin-top: 16px;
            border-radius: 4px;
            padding: 16px;
            background: ghostwhite;
            font-size: 14px;
            position: relative;
            overflow: hidden;
            cursor: pointer;

            &:first-of-type {
                margin-top: 0;
            }

            .article-author,
            .article-time {
                color: #9e9e9e;
            }

            .article-title {
                color: #212121;
                font-size: 18px;
                width: calc(100% - 112px);
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
            }

            .article-content {
                color: #888888;
                width: calc(100% - 112px);
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 2;
                overflow: hidden;
                margin: 10px 0;
            }
            .article-pic {
                position: absolute;
                width: 80px;
                height: 60px;
                right: 16px;
                top: 50%;
                transform: translateY(-50%);
            }

            .article-operate {
                position: absolute;
                top: 0;
                left: 110%;
                width: 100%;
                height: 100%;
                z-index: 2;
                background: rgba(135, 206, 235, 0.6);
                display: flex;
                justify-content: center;
                align-items: center;

                button {
                    margin-left: 12px;

                    &:first-of-type {
                        margin-left: 0;
                    }
                }
            }

            &:hover .article-operate {
                left: 0;
                animation: fadeInRight;
                animation-duration: 0.3s;
            }
        }
    }
}
</style>
